body {
  background-color: #04115a;
}

article {
  border-style: solid;
  border-color: gray;
  border-width: 5px;
  margin: 10px;
  padding: 20px;
}

p {
  color: gray;
  white-space: pre-line;
  font-size: 20px;
}

select {
  display: block;
  margin-top: 15px;
}

a:link {
  color: white;
}

a:visited {
  color: white;
}


.flashcards, .type {
  display: none;
}

.flashcard {
  word-wrap: break-word;
  display: flex;
  font-size: 20px;
  font-family: sans-serif;
  font-weight: bold;
  color: black;
  background-color: white;
  border: solid 2px black;
  width: 240px;
  height: 120px;
  padding-top: auto;
  padding-bottom: auto;
  text-align: center;
  justify-content: center;
  align-items: center;
  cursor: default;
  overflow: hidden;
}



.en, .attempt-display, .corrected-attempt-display, .corrections-display {
  color: white;
}
